import { hint, loadImage } from 'sol-common';
import Snap from 'sol-common/snap';
import { SOL } from 'sol-common/util';

import D1drag from '../components/D1drag';
import D1feedback from '../components/D1feedback';
import D1Animate from '../utils/D1Animate';

import { getPointInMatrixForm, getRandomArrayElement, shuffle, wait } from '../utils/util';

import D1circle from '../assets/images/D1circle.svg';
import D1rect from '../assets/images/D1rect.svg';
import D1triangle from '../assets/images/D1triangle.svg';

import backgroundImage from '../assets/testImages/background.svg';

import circleBear from '../assets/testImages/circle_bear.svg';
import circleLion from '../assets/testImages/circle_lion.svg';
import circleMonkey from '../assets/testImages/circle_monkey.svg';

import rectCow from '../assets/testImages/rect_cow.svg';
import rectGiraffe from '../assets/testImages/rect_giraffe.svg';
import rectZebra from '../assets/testImages/rect_zebra.svg';

import triangleDeer from '../assets/testImages/triangle_deer.svg';
import triangleFox from '../assets/testImages/triangle_fox.svg';
import triangleMouse from '../assets/testImages/triangle_mouse.svg';

export default function DM000001({ canvas = SOL.SVG, ...p }) {
    const g = canvas;
    const callback = p.callback;

    const background = p.background;
    const shapes = p.shapes;
    const drag = p.drag;

    const backgroundG = g.g().addClass('D1-background');
    const shapesG = g.g().addClass('D1-shapes');
    const dragG = g.g().addClass('D1-drag');
    const hintG = g.g().addClass('D1-hint');

    const items = [
        { type: 'circle', src: circleBear },
        { type: 'circle', src: circleLion },
        { type: 'circle', src: circleMonkey },
        { type: 'rect', src: rectCow },
        { type: 'rect', src: rectGiraffe },
        { type: 'rect', src: rectZebra },
        { type: 'triangle', src: triangleDeer },
        { type: 'triangle', src: triangleFox },
        { type: 'triangle', src: triangleMouse }
    ];

    let D1Shape = [];
    let D1DragItems = [];
    let dropItemBBoxes = [];

    let userAnswerCount = 0;
    let hintCount = 1;
    let isHint = true;
    let handHint = null;

    const drawBackground = () => {
        const { image: _image } = background;
        const { x, y } = _image;
        const backImage = loadImage({ src: backgroundImage, key: 'backgroundImage', type: 'svg' });
        backImage
            .use()
            .transform('t' + [x, y])
            .center()
            .appendTo(backgroundG);
    };

    const drawShapes = () => {
        const { generate, common, items } = shapes;
        const { centerOffest = { x: 0, y: 0 } } = common;
        const shapeImageSrc = { circle: D1circle, rect: D1rect, triangle: D1triangle };
        const shffleItems = shuffle(items).slice(0, generate.shapeNum);

        const point = getPointInMatrixForm({
            startPoint: { x: 400 + centerOffest.x, y: 200 + centerOffest.y },
            rows: 1,
            columns: shffleItems.length,
            gap: { x: common.gap, y: 0 },
            center: true
        });

        D1Shape = shffleItems.map((el, i) => {
            const { type, scale = common.scale, className = common.className, offset = common.offset || { x: 0, y: 0 } } = el;
            const itemG = shapesG.g().addClass('D1-shapes-item');
            const loadShape = loadImage({ src: shapeImageSrc[type], key: `D1shape-${type}`, type: 'svg', rx: 0, ry: 0 });
            const { w, h } = loadShape.getBBox();
            const { x, y } = point[i];

            const shape = loadShape
                .use()
                .transform('t' + [x + offset.x - w / 2, y + offset.y - h / 2] + 's' + scale)
                .appendTo(itemG);

            const original = shape.original.select('g').children()[1];
            original.addClass(className.default);
            dropItemBBoxes.push(shape.getBBox());

            shape.D1original = original;
            shape.D1type = type;
            shape.D1class = className;

            return shape;
        });
    };

    const drawDragItems = () => {
        const { generate, common } = drag;
        const { centerOffest = { x: 0, y: 0 } } = common;
        const shapeList = D1Shape.map(el => el.D1type);
        const shffleItems = shuffle(items.filter(el => shapeList.includes(el.type)));

        //그릴 이미지 선정
        const selectIndexList = shffleItems.map((el, i) => i);
        const selectIndex = shapeList.map(el => shffleItems.findIndex(el2 => el === el2.type)); // 초기 1개값 넣어주기
        const remainSelectIndexList = selectIndexList.filter(el => !selectIndex.includes(el));
        const remainDragNum = generate.dragNum - selectIndex.length;
        const remainSelectIndex = getRandomArrayElement(remainSelectIndexList, remainDragNum);
        const totalSelectIndex = [...selectIndex, ...remainSelectIndex];
        const shuffleTotalSelectIndex = shuffle(totalSelectIndex);

        const point = getPointInMatrixForm({
            startPoint: { x: 400 + centerOffest.x, y: 420 + centerOffest.y },
            rows: 1,
            columns: generate.dragNum,
            gap: { x: common.gap, y: 0 },
            center: true
        });

        D1DragItems = shuffleTotalSelectIndex.map((el, i) => {
            const target = shffleItems[el];
            const { type, src, scale = common.scale, offset = common.offset || { x: 0, y: 0 } } = target;
            const dragItemContainer = dragG.g().addClass('D1-drag-items');
            const dragItemG = dragItemContainer.g().addClass('D1-drag-item');
            const draggingItemG = dragItemContainer.g().addClass('D1-dragging-item');
            const { x, y } = point[i];

            const loadDragImage = loadImage({ src, key: `${type}-${i}`, type: 'svg' });
            const { w, h } = loadDragImage.getBBox();
            loadDragImage
                .use()
                .transform('t' + [x + offset.x - w / 2, y + offset.y - h / 2] + 's' + scale)
                .appendTo(dragItemG);
            loadDragImage
                .use()
                .transform('t' + [x + offset.x - w / 2, y + offset.y - h / 2] + 's' + scale)
                .appendTo(draggingItemG);

            dragItemG.D1type = type;

            return D1drag({
                dragItem: dragItemG,
                draggingItem: draggingItemG,
                startCallback: () => dragStartCallback(dragItemG),
                moveCallback: () => dragMoveCallback(dragItemG),
                endCallback: () => dragEndCallback(dragItemG)
            });
        });
    };

    const dragStartCallback = self => {
        dragG.append(self.parent());
        if (handHint) handHint.remove();
    };

    const dragMoveCallback = self => setShapeFillColor(self);

    const dragEndCallback = async self => {
        const dropIndex = getUserDropElementIndex(self);
        self.attr({ pointerEvents: 'none' });
        setShapeFillColor();
        if (typeof dropIndex === 'number') {
            const answer = self.D1type === D1Shape[dropIndex].D1type;
            if (answer) {
                self.done = true;
                self.dropIndex = dropIndex;
                await D1feedback({ el: self, bool: answer, scale: 0.8 });
                answerHandler();
            } else {
                await resetDragItem(self);
                self.attr({ pointerEvents: 'auto' });
            }
        } else {
            await resetDragItem(self);
            self.attr({ pointerEvents: 'auto' });
        }
        isHint && setHintState();
    };

    const answerHandler = async () => {
        userAnswerCount += 1;

        if (userAnswerCount === D1DragItems.length) {
            await rearrangeDragItems();
            await wait(100);
            D1Shape.forEach(el => el.remove());
            await wait(100);
            await shakeDragItem();
            await wait(100);
            callback();
        }

        async function rearrangeDragItems() {
            const animates = [];
            const shapeList = D1Shape.map(el => el.D1type);
            const shapeNum = D1DragItems.reduce((a, b) => {
                if (b.D1type) {
                    if (a[b.D1type]) a[b.D1type] += 1;
                    else a[b.D1type] = 1;
                }
                return a;
            }, {});
            const point = shapeList.map((el, i) => {
                const { cx, cy } = D1Shape[i].getBBox();
                const point = getPointInMatrixForm({
                    startPoint: { x: cx, y: cy },
                    rows: 1,
                    columns: shapeNum[el],
                    gap: { x: 90, y: 0 },
                    center: true
                });
                return { type: el, center: { cx, cy }, ...point };
            });

            point.forEach(el => {
                const targetList = D1DragItems.filter(el2 => el2.D1type === el.type);
                targetList.forEach((el3, i) => {
                    const { w, h } = el3.getBBox();
                    el3.moveAnimate && animates.push(el3.moveAnimate.bind(null, { x: el[i].x - w / 2, y: el[i].y - h / 2 }));
                });
            });

            await Promise.all(animates.map(el => el()));
        }
    };

    const resetDragItem = async dragItem => {
        const spectrum = dragItem.spectrumEffect({ interval: 120, duration: 750 });
        await dragItem.reset();
        spectrum.remove();
    };

    const shakeDragItem = async index => {
        if (typeof index === 'number') {
            const target = D1DragItems[index];
            target.attr({ pointerEvents: 'none' });
            await D1Animate.shake({ duration: 1000, rotate: 8, element: D1DragItems[index], reverse: Boolean(index % 2) });
            await D1Animate.shake({ duration: 1000, rotate: 10, element: D1DragItems[index], reverse: Boolean(index % 2) });
            target.attr({ pointerEvents: 'auto' });
        } else {
            const animate1 = [];
            const animate2 = [];
            D1DragItems.forEach((el, i) => {
                el.attr({ pointerEvents: 'none' });
                animate1.push(D1Animate.shake.bind(null, { duration: 1000, rotate: 8, element: el, reverse: Boolean(i % 2) }));
                animate2.push(D1Animate.shake.bind(null, { duration: 1000, rotate: 10, element: el, reverse: Boolean(i % 2) }));
            });

            await Promise.all(animate1.map(el => el()));
            await Promise.all(animate2.map(el => el()));

            D1DragItems.forEach(el => el.attr({ pointerEvents: 'auto' }));
        }
    };

    const getUserDropElementIndex = dragG => {
        const dragItemBBox = dragG.parent().getBBox();
        let targetIndex = undefined;
        const hintIndex = D1Shape.findIndex(el => el.D1type === dragG.D1type);
        const realDropBBoxes = isHint ? dropItemBBoxes.filter((el, i) => i === hintIndex) : dropItemBBoxes;

        const size = 20;
        const { x, x2, y, y2, width, height } = dragItemBBox;
        const adjustBBox = { x: x + size, x2: x2 - size, y: y + size, y2: y2 - size, width: width - 2 * size, height: height - 2 * size };

        realDropBBoxes.some((el, i) => {
            if (Snap.path.isBBoxIntersect(adjustBBox, el)) {
                targetIndex = isHint ? hintIndex : i;
                return true;
            } else {
                targetIndex = undefined;
                return false;
            }
        });
        return targetIndex;
    };

    const setShapeFillColor = dragG => {
        const dropIndex = dragG && getUserDropElementIndex(dragG);
        if (typeof dropIndex === 'number') {
            D1Shape.forEach((el, i) => {
                const { D1class, D1original } = el;
                const target = dropIndex === i;
                const classes = [D1class.default, D1class.choice];
                D1original.removeClass(classes[Number(!target)]).addClass(classes[Number(target)]);
            });
        } else {
            D1Shape.forEach(el => {
                const { D1class, D1original } = el;
                D1original.removeClass(D1class.choice).addClass(D1class.default);
            });
        }
    };

    const setHintState = () => {
        D1DragItems.forEach(el => el.attr({ pointerEvents: 'none' }));
        if (userAnswerCount < hintCount) {
            const targetDragItem = D1DragItems[userAnswerCount].attr({ pointerEvents: 'auto' });
            const targetDropElement = D1Shape.find(el => el.D1type === targetDragItem.D1type);
            const { cx: dragX, cy: dragY } = targetDragItem.getBBox();
            const { cx: dropX, cy: dropY } = targetDropElement?.getBBox() || {};
            handHint = hint({ canvas: hintG, scale: 0.8, type: 1, XY: [dragX, dragY], moveTo: [dropX, dropY], pathStr: undefined });
        } else {
            D1DragItems.forEach(el => !el.done && el.attr({ pointerEvents: 'auto' }));
            isHint = false;
        }
    };

    const start = async () => {
        drawBackground();
        drawShapes();
        drawDragItems();
        await shakeDragItem();
        isHint && setHintState();
    };

    start();
}
